<template>
    
</template>

<script>
    export default {
        name: "oem",
        inject: ['reloadSetup'],
        created() {
            this.showLoading();
            if (this.$route.query.hasOwnProperty('idweb')) {
                let web_id = this.$route.query.idweb;
                web_id = parseInt(web_id);
                if (!isNaN(web_id)) {
                    this.$setConfig('web_id',web_id);
                }
            }
            if (this.$route.query.hasOwnProperty('lang')) {
                let lang = this.$route.query.lang;
                lang = parseInt(lang);
                if (!isNaN(lang)) {
                    this.$lang('lang_' + lang);
                }
            }
            if (this.$route.query.hasOwnProperty('type')) {
                this.$oem(this.$route.query.type==1);
            }else{
                this.$oem(false);
            }
            if (this.$route.query.hasOwnProperty('server')) {
                this.$serverUrl(this.$route.query.server);
            }else{
                this.$serverUrl(null);
            }
            let redirect = null;
            if (this.$route.query.hasOwnProperty('redirect')) {
                redirect = this.$route.query.redirect;
            }
            this.$isPublish(true);
            this.reloadSetup(()=>{
                if (redirect) {
                    window.location.href = redirect;
                }else{
                    this.$router.push({name:'home'});
                }
            });
        },
        methods:{
            showLoading() {
                this.$loading({
                    lock: true,
                });
            },
        }
    }
</script>